body {
  min-height: 100vh;
  background-repeat: no-repeat;
}
.cotations {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.container {
  /* min-height: 80vh; */
  width: 95%;
  margin-top: 10px;
  border-radius: 10px;
  background: white;
}
.cotation {
  position: relative;
  width: 30%;
  justify-content: center;

  display: inline-flex;
  flex-direction: column;
  color: white;
  min-height: 40vh;
  background: #3c6943;
  border-radius: 10px;
  margin: 10px;
  overflow: auto;
}

#date {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
}
#num {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px;
}
.flex {
  /* width: 50%; */
  text-align: center;
  margin: 10px;
  background: white;
  font-weight: bolder;
  border-radius: 10px;
  color: black;

  word-wrap: break-word; /* IE */
}
.cot {
  margin: 5px;
  /* border: none; */
  width: 32%;
  border-radius: 10px;
  padding: 5px;
  font-weight: bold;
  color: #3c6943;
  background: white;
}

.cot:hover{
  background:#67ac71 ;
  color: white;
}