.header{
    height: 10vh;
    width: 100%;
    background-color : rgb(63, 133, 63);
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
}

.liens{
    padding: 25px;
}
.liens span{
    padding: 5px;
}
a{
    text-decoration: none;
    border : 10px ;
    background: rgb(87, 87, 165);
    border-radius: 10px;
    color: white;
    padding: 10px;
    transition: 0.3s;
    font-weight: bold;
}
a:hover{
    color: rgb(87, 87, 165);
    background: white;
}
.logo img{
    padding: 20px;
}
.active{
    background : white;
    color: rgb(137, 43, 156);
    text-decoration: underline;
}
.bttn{
 cursor: pointer;
    border : 10px ;
    background: rgb(87, 87, 165);
    border-radius: 10px;
    color: white;
    padding: 10px;
    transition: 0.3s;
    font-weight: bold;
}

.bttn:hover{
    color: rgb(87, 87, 165);
    background: white;
}