.new {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

.CotationForm {
  padding: 25px;

  border-radius: 10px;
  width: 25%;
  background: white;
  height: 75%;
}
input {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 15px;
  border-radius: 10px;
}
textarea {
  margin: 10px 0;
  padding: 15px;
}
.btn {
  margin: 5px;
  background: #3498db;
  background-image: -webkit-linear-gradient(top, #3498db, #2980b9);
  background-image: -moz-linear-gradient(top, #3498db, #2980b9);
  background-image: -ms-linear-gradient(top, #3498db, #2980b9);
  background-image: -o-linear-gradient(top, #3498db, #2980b9);
  background-image: linear-gradient(to bottom, #3498db, #2980b9);
  -webkit-border-radius: 28;
  -moz-border-radius: 28;
  border-radius: 28px;
  -webkit-box-shadow: 0px 1px 3px #666666;
  -moz-box-shadow: 0px 1px 3px #666666;
  box-shadow: 0px 1px 3px #666666;
  font-family: Arial;
  color: #ffffff;
  font-size: 23px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
}

.btn:hover {
  background: #3c46fc;
  background-image: -webkit-linear-gradient(top, #3c46fc, #76a1b0);
  background-image: -moz-linear-gradient(top, #3c46fc, #76a1b0);
  background-image: -ms-linear-gradient(top, #3c46fc, #76a1b0);
  background-image: -o-linear-gradient(top, #3c46fc, #76a1b0);
  background-image: linear-gradient(to bottom, #3c46fc, #76a1b0);
  text-decoration: none;
}

.small{
  padding: 9px;
  font-size: 15px;
}